import React, { useState } from "react";
import { Button, Card, Row, Col } from "react-bootstrap";
import img1 from "./img1.jpeg";
import img2 from "./img2.jpeg";

function CurrentServices() {
  const [activeType, setActiveType] = useState();

  const services = [
    {
      id: 1,
      type: "",
      title: "       ",
      date: "23//2024",
      description:
        "          .              .                   .                .               .",
      image: img1,
    },
    {
      id: 2,
      type: "",
      title: '  "  "',
      date: "6//2024",
      description:
        '      "  "        .               .            .        .          !',
      image: img2,
    },
    {
      id: 3,
      type: "",
      title: '  "  "',
      date: "6//2024",
      description:
        '      "  "        .               .            .        .          !',
      image: img2,
    },

    {
      id: 4,
      type: "",
      title: "       ",
      date: "23//2024",
      description:
        "          .              .                   .                .               .",
      image: img1,
    },

    {
      id: 5,
      type: "",
      title: "       ",
      date: "23//2024",
      description:
        "          .              .                   .                .               .",
      image: img1,
    },
    {
      id: 6,
      type: "",
      title: '  "  "',
      date: "6//2024",
      description:
        '      "  "        .               .            .        .          !',
      image: img2,
    },
    {
      id: 7,
      type: "",
      title: '  "  "',
      date: "6//2024",
      description:
        '      "  "        .               .            .        .          !',
      image: img2,
    },
    {
      id: 8,
      type: "",
      title: "       ",
      date: "23//2024",
      description:
        "          .              .                   .                .               .",
      image: img1,
    },
  ];

  const categories = [...new Set(services.map((service) => service.type))];

  const filteredServices = services.filter(
    (service) => service.type === activeType
  );

  return (
    <div className="mt-4">
      <div className="services-tabsBtns">
        {categories.map((category) => (
          <button
            key={category}
            className={
              activeType === category
                ? "services-custom-active-btn"
                : "services-custom-inactive-btn"
            }
            onClick={() => setActiveType(category)}
          >
            {category}
          </button>
        ))}
      </div>

      <Row>
        {filteredServices.map((service) => (
          <Col key={service.id} className="mb-4 services-col">
            <card className="services-card">
              <Row className="g-0 services-cardRow">
                <Col>
                  <img
                    className="services-CardImg"
                    src={service.image}
                    alt={service.title}
                  />
                </Col>
                <Col className=" col-md-9 services-Box">
                  <div className="d-flex justify-content-end p-3">
                    <header className="services-CardTitle">
                      {service.title}
                    </header>
                    <button className="services-cardBtn"></button>
                    <button className="services-cardBtn2"></button>
                  </div>

                  <Card.Text className="servicesDate">
                    &#8226; {service.date}
                  </Card.Text>
                  <Card.Text className="services-CardText">
                    {service.description}
                  </Card.Text>
                </Col>
              </Row>
            </card>
          </Col>
        ))}
      </Row>
    </div>
  );
}

export default CurrentServices;
